import {slide as Menu} from 'react-burger-menu'
import React, { Component } from 'react'
import styles from './AppComponentStyles/AppSideNav.css'
import SideBarMenu from './AppSideBar.js'
import {TreeNode, TreeMenu} from 'react-tree-menu'


// Dummy component, should only be getting data from
// components from the AppBusinessLogic folder. 
export default class SideNavCustom extends Component{
	showSettings(event){
		event.preventDefault();
	}
	render(){
		// we pass the data into our components
		return(
			<Menu styles= {styles}  isOpen={true} noOverlay width={'180px'}>
				<img src="https://png.icons8.com/user/androidL/96" class="user" title="User" width="96" height="96" />
				 <a id="userEmail" className="menu-item" href="/">Click here to log in</a>
				 <TreeMenu
			        sort
			        identifier="id"
			        onTreeNodeClick={this._setLastActionState.bind(this, "clicked", "2")}
			        onTreeNodeCollapseChange={this._handleDeclarativeTreeNodePropChange.bind(this, "collapsed")}
			        onTreeNodeCheckChange={this._handleDeclarativeTreeNodePropChange.bind(this, "checked")}
			        collapsible={false}
			        expandIconClass="fa fa-chevron-right"
			        collapseIconClass="fa fa-chevron-down">
			        <TreeNode label="Option 1" id="option_1"/>
			        <TreeNode label="Option 2" id="option_2">
			          <TreeNode label="Option B"  id="option_2.b"/>
			          <TreeNode label="Option A"  id="option_2.a"/>
			        </TreeNode>
			        <TreeNode label="Option 3" id="option_3"/>
			        <TreeNode label="Option 4" id="option_4"/>
			      </TreeMenu>
			</Menu>
		);
	}

}



